"use client"

import type React from "react"
import { LayoutWrapper } from "@/components/layout-wrapper"
import { Search, Plus, Mail, Phone, Edit2, File, X } from "lucide-react"
import { useState, useEffect } from "react"
import { validateEmail, validatePhone } from "@/lib/auth"
import { getCurrentUser, checkPermission } from "@/lib/constants"
import { logActivity } from "@/lib/utils"

interface Patient {
  id: string
  name: string
  age: number
  gender: "Male" | "Female"
  phone: string
  email: string
  address?: string
  attachments?: string[]
}

export default function PatientsPage() {
  const [patients, setPatients] = useState<Patient[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [sortBy, setSortBy] = useState("name")
  const [filterGender, setFilterGender] = useState("")
  const [showAddForm, setShowAddForm] = useState(false)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [newPatient, setNewPatient] = useState<Partial<Patient>>({
    name: "",
    age: 0,
    gender: "Male",
    phone: "",
    email: "",
    address: "",
  })
  const [formErrors, setFormErrors] = useState<string[]>([])
  const [user, setUser] = useState<any>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const currentUser = getCurrentUser()
    if (currentUser) setUser(currentUser)
  }, [])

  useEffect(() => {
    if (user) fetchPatients()
  }, [user])

  const fetchPatients = async () => {
    if (!user) return
    try {
      setIsLoading(true)
      const response = await fetch("/api/patients", {
        headers: { "x-user-id": user.id },
      })
      if (response.ok) {
        const data = await response.json()
        setPatients(data.patients || [])
      }
    } catch (error) {
      console.error("Error fetching patients:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const canManagePatients = user && checkPermission(user.role, "canManagePatients")
  const canEditPatients = user && checkPermission(user.role, "canEditPatients")

  const filteredPatients = patients
    .filter(
      (p) =>
        (p.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
          p.email.toLowerCase().includes(searchTerm.toLowerCase())) &&
        (!filterGender || p.gender === filterGender)
    )
    .sort((a, b) => {
      if (sortBy === "name_az") return a.name.localeCompare(b.name)
      if (sortBy === "name_za") return b.name.localeCompare(a.name)
      return 0
    })

  const handleAddPatient = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!canManagePatients) return

    const errors: string[] = []

    if (!newPatient.name?.trim()) errors.push("Name is required")
    if (!newPatient.age || newPatient.age <= 0) errors.push("Valid age is required")
    if (!validatePhone(newPatient.phone || "")) errors.push("Phone must be numbers only")
    if (!validateEmail(newPatient.email || "")) errors.push("Email must contain @")

    if (errors.length > 0) {
      setFormErrors(errors)
      return
    }

    try {
      const method = editingId ? "PUT" : "POST"
      const body = editingId ? { id: editingId, ...newPatient } : newPatient

      const response = await fetch("/api/patients", {
        method,
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify(body),
      })

      if (response.ok) {
        logActivity(
          "patient",
          editingId ? `Updated patient: ${newPatient.name}` : `Added new patient: ${newPatient.name}`
        )
        await fetchPatients()
        setEditingId(null)
      }

      setNewPatient({ name: "", age: 0, gender: "Male", phone: "", email: "", address: "" })
      setShowAddForm(false)
      setFormErrors([])
    } catch (error) {
      console.error("Error saving patient:", error)
      setFormErrors(["Failed to save patient. Please try again."])
    }
  }

  const handleEditPatient = (patient: Patient) => {
    if (!canEditPatients) return
    setEditingId(patient.id)
    setNewPatient(patient)
    setShowAddForm(true)
  }

  const handleCancelEdit = () => {
    setEditingId(null)
    setNewPatient({ name: "", age: 0, gender: "Male", phone: "", email: "", address: "" })
    setFormErrors([])
    setShowAddForm(false)
  }

  const handleDeletePatient = async (id: string) => {
    if (!confirm("Are you sure you want to delete this patient?")) return
    try {
      const response = await fetch("/api/patients", {
        method: "DELETE",
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify({ id }),
      })
      if (response.ok) await fetchPatients()
    } catch (error) {
      console.error("Error deleting patient:", error)
    }
  }

  return (
    <LayoutWrapper>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-[#8B3A3A]">Patients</h1>
            <p className="text-gray-600">Manage patient records and information</p>
          </div>
          {canManagePatients && (
            <button
              onClick={() => {
                setShowAddForm(true)
                setEditingId(null)
                setNewPatient({ name: "", age: 0, gender: "Male", phone: "", email: "", address: "" })
              }}
              className="bg-[#8B3A3A] text-white px-6 py-3 rounded-lg font-semibold hover:bg-[#6D2E2E] flex items-center gap-2"
            >
              <Plus size={20} /> Add Patient
            </button>
          )}
        </div>

        {/* Search & Filter */}
        <div className="bg-[#8B3A3A] rounded-xl p-6 flex gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-4 top-3 text-white opacity-60" size={20} />
            <input
              type="text"
              placeholder="Search patients by name or email..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-12 pr-4 py-2 bg-white/20 text-white placeholder-white/60 rounded-lg focus:outline-none focus:ring-2 focus:ring-white"
            />
          </div>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="bg-white text-[#8B3A3A] px-4 py-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-white font-medium cursor-pointer"
          >
            <option value="name">Sort</option>
            <option value="name_az">A to Z</option>
            <option value="name_za">Z to A</option>
          </select>
          <select
            value={filterGender}
            onChange={(e) => setFilterGender(e.target.value)}
            className="bg-white text-[#8B3A3A] px-4 py-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-white font-medium cursor-pointer"
          >
            <option value="">All Gender</option>
            <option value="Male">Male</option>
            <option value="Female">Female</option>
          </select>
        </div>

        {/* Add / Edit Form */}
        {showAddForm && canManagePatients && (
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-xl font-bold">{editingId ? "Edit Patient" : "Add New Patient"}</h2>
              <button onClick={handleCancelEdit} className="text-gray-500 hover:text-gray-700">
                <X size={24} />
              </button>
            </div>
            {formErrors.length > 0 && (
              <div className="bg-red-50 border border-red-200 rounded-lg p-3 mb-4">
                {formErrors.map((error, i) => (
                  <p key={i} className="text-red-600 text-sm">{error}</p>
                ))}
              </div>
            )}
            <form onSubmit={handleAddPatient} className="space-y-4">
              {/* Name & Age */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold mb-2">Name</label>
                  <input
                    type="text"
                    value={newPatient.name || ""}
                    onChange={(e) => setNewPatient({ ...newPatient, name: e.target.value })}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                  />
                </div>
                <div>
                  <label className="block text-sm font-semibold mb-2">Age</label>
                  <input
                    type="number"
                    value={newPatient.age || ""}
                    onChange={(e) => setNewPatient({ ...newPatient, age: Number(e.target.value) || 0 })}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                  />
                </div>
              </div>

              {/* Gender & Phone */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold mb-2">Gender</label>
                  <select
                    value={newPatient.gender || "Male"}
                    onChange={(e) => setNewPatient({ ...newPatient, gender: e.target.value as "Male" | "Female" })}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                  >
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-semibold mb-2">Phone</label>
                  <input
                    type="text"
                    value={newPatient.phone || ""}
                    onChange={(e) => setNewPatient({ ...newPatient, phone: e.target.value })}
                    placeholder="Numbers only"
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                  />
                </div>
              </div>

              {/* Email & Address */}
              <div>
                <label className="block text-sm font-semibold mb-2">Email</label>
                <input
                  type="email"
                  value={newPatient.email || ""}
                  onChange={(e) => setNewPatient({ ...newPatient, email: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                />
              </div>
              <div>
                <label className="block text-sm font-semibold mb-2">Address</label>
                <input
                  type="text"
                  value={newPatient.address || ""}
                  onChange={(e) => setNewPatient({ ...newPatient, address: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                />
              </div>

              <div className="flex gap-2">
                <button
                  type="submit"
                  className="bg-[#8B3A3A] text-white px-6 py-2 rounded-lg font-semibold hover:bg-[#6D2E2E]"
                >
                  {editingId ? "Update Patient" : "Add Patient"}
                </button>
                <button
                  type="button"
                  onClick={handleCancelEdit}
                  className="bg-gray-200 text-gray-900 px-6 py-2 rounded-lg font-semibold"
                >
                  Cancel
                </button>
              </div>
            </form>
          </div>
        )}

        {/* Patient List */}
        <div className="space-y-4">
          {filteredPatients.length > 0 ? (
            filteredPatients.map((patient) => (
              <div key={patient.id} className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="text-xl font-bold">{patient.name}</h3>
                    <p className="text-sm text-gray-600">{patient.age} years old - {patient.gender}</p>
                  </div>
                  {canEditPatients && (
                    <button
                      onClick={() => handleEditPatient(patient)}
                      className="bg-[#8B3A3A] text-white px-4 py-2 rounded-lg font-semibold hover:bg-[#6D2E2E] flex items-center gap-2"
                    >
                      <Edit2 size={18} /> Edit
                    </button>
                  )}
                </div>
                <div className="mt-2 text-gray-700 space-y-1">
                  <p><Phone className="inline mr-2" size={16} /> {patient.phone}</p>
                  <p><Mail className="inline mr-2" size={16} /> {patient.email}</p>
                  {patient.address && <p><strong>Address:</strong> {patient.address}</p>}
                </div>
              </div>
            ))
          ) : (
            <div className="text-center py-12">
              <p className="text-gray-500">No patients found</p>
            </div>
          )}
        </div>
      </div>
    </LayoutWrapper>
  )
}
